/* _layout.css */

/* Sitewide wrapper */
.site {
  padding-left: var(--gutter);
  padding-right: var(--gutter);
}

/* Sitewide landmarks */
.site__header,
.site__main,
.site__footer {
  margin-left: auto;
  margin-right: auto;
  max-width: var(--max-width);
}

/* Main */
.site__main {
  margin-bottom: var(--grid-gap);
  margin-top: var(--grid-gap);
}

@mixin stack .homepage, var(--grid-gap);
